@use 'sass:color';

// Font Family
$font-0: "Sora-Regular", sans-serif;
$font-1: "Sora-SemiBold", sans-serif;
$font-3: 'Gambetta-Regular', sans-serif;
$font-4: 'Gambetta-Italic', sans-serif;
$font-5: 'Gambetta-Medium', sans-serif;
$font-6: 'Gambetta-MediumItalic', sans-serif;


// Colors ---------------
$white: #fff;
$black: #222;
$gray: #F9F9F9;


// Theme Color
// $theme-color: #726242;
$theme-color: #726242;


//Body Color
$body-color: #2a362f;


// Border Color
$border-color: #232c27;


// Heading Color
$heading-color: #3d372a;


// Soft color
$black-soft:#7e7e7e;


// Responsive Variables
$desktop: 'only screen and (min-width: 1400px)';
$laptop: 'only screen and (min-width: 1200px) and (max-width: 1399px)';
$lg : 'only screen and (min-width: 992px) and (max-width: 1199px)';
$md:'only screen and (min-width: 768px) and (max-width: 991px)';
$xs:'(max-width: 767px)';
$sm: 'only screen and (min-width: 576px) and (max-width: 767px)';


