html {
    scroll-behavior: smooth;
}

body {
    font-family: $font-3;
    font-weight: normal;
    font-style: normal;
    font-size: 18px;
    line-height: 1.4;
    color: $body-color;
    background-color: rgb(214, 214, 214);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

img {
    max-width: 100%;
}

a:focus,
input:focus,
textarea:focus,
button:focus {
    text-decoration: none;
    outline: none;
}

a:focus,
a:hover {
    text-decoration: none;
}

i,
span,
a {
    display: inline-block;
    text-decoration: none;
}

audio,
canvas,
iframe,
img,
svg,
video {
    vertical-align: middle;
}


h1,
h2,
h3,
h4,
h5,
h6 {
    // font-family: $font-1;
    font-family: $font-1;
    font-weight: 700;
    color: $heading-color;
    margin: 0px;
}

h1 {
    font-size: 48px;
}

h2 {
    font-size: 36px;
}

h3 {
    font-size: 28px;
}

h4 {
    font-size: 22px;
}

h5 {
    font-size: 18px;
}

h6 {
    font-size: 16px;
}

ul,
ol {
    margin: 0px;
    padding: 0px;
    list-style-type: none;
}

p {
    font-size: 18px;
    font-weight: 400;
    line-height: 1.4;
    color: $body-color;
    margin: 0px;
}

.bg_cover {
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
}

// others common css here

.cat-icon {
    // background-color: rgb(249, 230, 230);
    svg {
        height: 72px;
        width: 72px;
    }
}


/*===== All Button Style =====*/

.main-btn {
    display: inline-block;
    font-weight: 500;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;
    border: 0;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-family: $font-5;
    padding: 0 40px;
    font-size: 16px;
    height: 55px;
    line-height: 55px;
    border-radius: 0;
    color: $white;
    cursor: pointer;
    z-index: 5;
    background-color: $theme-color;
    @include transition(0.25s);
    &:hover {
        background-color: color.adjust($theme-color, $lightness: 10%);
        color: $white;
    }

    &-sm {
        letter-spacing: 1px;
        font-size: 15px;
        height: 40px;
        line-height: 40px;
        padding: 0 20px;
    }
}

/*===== All Section Title Style =====*/

.nav-link {
    background-color: white;
}

.nav-pills .nav-link.active, .nav-pills .show > .nav-link {
    color: #fff;
  background-color: $theme-color;
}

.tab-pane .active {
    background-color: red;
}



.section_title {
    & .title {
        font-size: 40px;
        font-weight: 700;

        @media #{$xs} {
            font-size: 26px;
        }
    }

    & .subtitle {
        margin-top: 14px;
        font-size: 22px;
        font-weight: 400;
        font-family: $font-4;

        @media #{$xs} {
            font-size: 22px;
        }
    }

    & p {
        margin-top: 15px;
        font-size: 18px;
        line-height: 1.5;
        color: $black;
    }

    &.section_title_2 {

        & .title,
        & p {
            color: $white;
        }
    }
}



/*===== All Preloader Style =====*/

.preloader {
    /* Body Overlay */
    position: fixed;
    top: 0;
    left: 0;
    display: table;
    height: 100%;
    width: 100%;
    /* Change Background Color */
    background: $white;
    z-index: 99999;

    & .loader {
        display: table-cell;
        vertical-align: middle;
        text-align: center;

        & .ytp-spinner {
            position: absolute;
            left: 50%;
            top: 50%;
            width: 64px;
            margin-left: -32px;
            z-index: 18;
            pointer-events: none;

            & .ytp-spinner-container {
                pointer-events: none;
                position: absolute;
                width: 100%;
                padding-bottom: 100%;
                top: 50%;
                left: 50%;
                margin-top: -50%;
                margin-left: -50%;
                @include animation(ytp-spinner-linspin 1568.23529647ms linear infinite);

                & .ytp-spinner-rotator {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    @include animation(ytp-spinner-easespin 5332ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both);

                    & .ytp-spinner-left {
                        position: absolute;
                        top: 0;
                        left: 0;
                        bottom: 0;
                        overflow: hidden;
                        right: 50%;
                    }

                    & .ytp-spinner-right {
                        position: absolute;
                        top: 0;
                        right: 0;
                        bottom: 0;
                        overflow: hidden;
                        left: 50%;
                    }
                }
            }
        }

        & .ytp-spinner-circle {
            box-sizing: border-box;
            position: absolute;
            width: 200%;
            height: 100%;
            border-style: solid;
            /* Spinner Color */
            border-color: $theme-color $theme-color $gray;
            border-radius: 50%;
            border-width: 6px;
        }

        & .ytp-spinner-left {
            & .ytp-spinner-circle {
                left: 0;
                right: -100%;
                border-right-color: $gray;
                @include animation(ytp-spinner-left-spin 1333ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both);
            }
        }

        & .ytp-spinner-right {
            & .ytp-spinner-circle {
                left: -100%;
                right: 0;
                border-left-color: $gray;
                @include animation(ytp-right-spin 1333ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite both);
            }
        }
    }
}

/* Preloader Animations */

@-webkit-keyframes ytp-spinner-linspin {
    to {
        @include transform(rotate(360deg));
    }
}

@keyframes ytp-spinner-linspin {
    to {
        @include transform(rotate(360deg));
    }
}

@-webkit-keyframes ytp-spinner-easespin {
    12.5% {
        @include transform(rotate(135deg));
    }

    25% {
        @include transform(rotate(270deg));
    }

    37.5% {
        @include transform(rotate(405deg));
    }

    50% {
        @include transform(rotate(540deg));
    }

    62.5% {
        @include transform(rotate(675deg));
    }

    75% {
        @include transform(rotate(810deg));
    }

    87.5% {
        @include transform(rotate(945deg));
    }

    to {
        @include transform(rotate(1080deg));
    }
}

@keyframes ytp-spinner-easespin {
    12.5% {
        @include transform(rotate(135deg));
    }

    25% {
        @include transform(rotate(270deg));
    }

    37.5% {
        @include transform(rotate(405deg));
    }

    50% {
        @include transform(rotate(540deg));
    }

    62.5% {
        @include transform(rotate(675deg));
    }

    75% {
        @include transform(rotate(810deg));
    }

    87.5% {
        @include transform(rotate(945deg));
    }

    to {
        @include transform(rotate(1080deg));
    }
}

@-webkit-keyframes ytp-spinner-left-spin {
    0% {
        @include transform(rotate(130deg));
    }

    50% {
        @include transform(rotate(-5deg));
    }

    to {
        @include transform(rotate(130deg));
    }
}

@keyframes ytp-spinner-left-spin {
    0% {
        @include transform(rotate(130deg));
    }

    50% {
        @include transform(rotate(-5deg));
    }

    to {
        @include transform(rotate(130deg));
    }
}

@-webkit-keyframes ytp-right-spin {
    0% {
        @include transform(rotate(-130deg));
    }

    50% {
        @include transform(rotate(5deg));
    }

    to {
        @include transform(rotate(-130deg));
    }
}

@keyframes ytp-right-spin {
    0% {
        @include transform(rotate(-130deg));
    }

    50% {
        @include transform(rotate(5deg));
    }

    to {
        @include transform(rotate(-130deg));
    }
}


