/*===========================
      03.FEATURES css 
===========================*/

@use './variables' as *;
@use 'mixin' as *;
@use 'sass:color';


.card-link {
    color: $theme-color;
    // font-weight: 600;
}

.features_area {
    background-color: rgba($color: $theme-color, $alpha: 0.01)
}

.features_area_2 {
    background-color: color.adjust($theme-color, $lightness: 55%);
}

.single_features {
    & i {
        font-size: 60px;
        line-height: 60px;
        color: $theme-color;
        @include transition(0.3s);

        @media #{$xs} {
            font-size: 40px;
            line-height: 45px;
        }
    }

    & .features_title {
        color: $theme-color;
        margin-top: 15px;
        font-size: 24px;
        font-weight: 600;
        text-transform: lowercase;
        text-transform: capitalize;
        @include transition(0.3s);

        @media #{$xs} {
            font-size: 24px;
        }

    }

    & .features_subtitle {
        color: $theme-color;
        margin-top: 15px;
        font-size: 20px;
        font-weight: 400;
        @include transition(0.3s);

        @media #{$xs} {
            font-size: 20px;
        }

    }

    & p {
        margin-top: 18px;
    }

    &:hover {
        & i {
            color: $theme-color;
        }
    }
}